module.exports = "<div v-if=isShow><div class=mark_bg></div><div class=dialogBox><div class=dialog-title v-if=\"msg.title!=='' && msg.type=='alert' || msg.type=='confirm' || msg.type=='prompt'\">{{msg.title}}</div><div class=dialog-content v-if=\"msg.des!='' && msg.type=='alert' || msg.type=='confirm'\">{{msg.des}}</div><div class=dialog-content v-if=\"msg.type=='prompt'\"><div class=tag_content><div class=input><input v-model=mycontent></div></div></div><div class=\"btn_box btn_box1\" v-if=\"msg.type=='alert'\"><ul class=btn1><li><button @click=closeDialog()>确定</button></li></ul></div><div class=btn_box v-if=\"msg.type=='confirm' || msg.type=='prompt'\"><ul class=btn2><li class=btn_red><button @click=closeDialog>取消</button></li><li class=btn_grey><button @click=okDialog v-if=\"msg.type=='confirm'\">确定</button> <button @click=confirmDialog v-if=\"msg.type=='prompt'\">确定</button></li></ul></div></div></div>";


